body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  background-color:	#CAE4DB;
  color: #00303F;
}

.weatherApp {
  text-align: center;
  height: 100vh;


  
}


.submitBox{
    font-size: 30px;
    border-radius: 6px;
    padding: 0.6% 5%;
    
    background-color:#CAE4DB;
    color: #00303F;
}
.textInput{
    width: 40%;
    font-size: 40px;
    border-radius: 6px;

    margin-bottom: 2%;
    margin-right: 2%;
    
}

.formContainer{
    margin-left: 25%;
    margin-right: 25%;
    background-color: #7A9D96;
    color: white;
    padding: 0.3% 0.6%;	
    
}

.iconClockContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

time{
    padding-top: 17px;
    font-size: 50px;
}

.label{
    font-size: 50px;
    text-align: left;
    margin-left: 1%;
    margin-top: 1%;
}

.textAndSubmitComponents{
    display: inline-block;
}

h4{
    display: inline;
    font-size: 50px;
}

.cityName{
    font-size: 70px;
}



.forecastContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-left: 20%;
    margin-right: 20%;

}

.error{
    background-color:  #800000;
    color: white;
    font-size: 50px;
    margin: 1% 25% 0;
}
